@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p{

  color: #43bbc2;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{font-family:'Raleway';color:black}.light{font-weight:300;font-size:14px;color:black}.medium{font-weight:400;font-size:16px}.bold{font-weight:700;font-size:32px}p,h1,h2,h3,h6{color:black}p,h2,h1,h3,h6{text-align:left}.header{display:flex;justify-content:space-between;padding:2rem;width:100%}.header .logo{width:250px}.header .nav-list{display:flex}.header .nav-list li{list-style:none;font-weight:500;padding:1rem;color:black;text-transform:capitalize}.home{width:50%}.home .title{font-family:serif}.home .description{color:black}.about{width:70%}.publications{width:50%;min-width:600px}.publications p:nth-child(2){margin-bottom:3rem}.publications p:nth-child(2):hover{cursor:default;text-decoration:none}.publications h3,.publications p{margin:2rem 0}.publications p{font-size:18px;font-weight:500}.publications p:hover{cursor:pointer;text-decoration:underline}a{text-decoration:none;cursor:pointer}.App{display:flex;flex-direction:column;align-items:center;justify-content:space-between;min-height:100vh}*{box-sizing:border-box}

